import { Component, OnInit } from '@angular/core';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { Menu } from '../../_models/Menu';
import { AlertifyService } from '../../_services/alertify.service';
import { RoleService } from '../../_services/role.service';
import { Role } from '../../_models/Role';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.css']
})
export class MenuComponent implements OnInit {
  model: any = {};
  setrole: string;
  menus: Menu[];
  roles: Role[];

  constructor(private loadspecsService: LoadspecsService, private roleService: RoleService,
                   private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.model.roleID = '0';
    this.loadRoles();
  }
  loadMenu(id) {
    console.log(id);
    return this.loadspecsService.getallmenus().subscribe((menus: Menu[]) => {
      this.menus = menus.filter(proj => (proj.role_id === Number(id)));
      console.log(this.menus);
    });
  }
  loadRoles() {
    return this.roleService.getRoles().subscribe((roles: Role[]) => {
      this.roles = roles;
      console.log(this.roles);
    }, error => {
      this.alertify.error(error);
    });
  }
  activateMenu(event, id) {
    this.setrole  = this.model.roleID;
    this.model = {id : id, is_active: event.target.checked};
    console.log(this.model);
    this.loadspecsService.activateMenu(this.model).subscribe(() => {
        this.model.roleID = this.setrole;
        this.loadMenu(this.setrole);
    }, error => {
      this.alertify.error(error);
    });
  }

}
